/* globals requirejs */

import Ratchet from 'ratchet/web';
import { name as moduleId } from './module.json';

function getModule() {
  const {
    'registered-modules': registeredModules
  } = Ratchet.observable('oneteamApplication').get();

  let ehModule;

  for (let i = 0, l = registeredModules.length; i < l; i += 1) {
    const mod = registeredModules[i];

    if (mod.id === moduleId) {
      ehModule = mod;
      break;
    }
  }

  return ehModule;
}

function getEnvUrl({
  env,
  projectId,
  module: {
    id,
    timestamp
  }
}) {
  const modUrl = `${window.location.origin}/_modules-${timestamp}/${id}`;

  if (projectId) {
    return `${modUrl}/projects/${projectId}/${env}.js`;
  }

  return `${modUrl}/${env}.js`;
}

function getEnvFromBranch(branch) {
  const { title } = branch;

  if (title.startsWith('DEV: ') || title.startsWith('Pre-Production - ')) {
    return 'preproduction';
  }

  if (title.startsWith('PR: ')) {
    return 'prs';
  }

  if (title.startsWith('STAGING: ') || title.startsWith('STAGING - ')) {
    return 'staging';
  }

  return 'production';
}

function getEnvFromSessionStorage() {
  if (typeof Storage !== 'undefined') {
    return window.sessionStorage.getItem('moduleEnv');
  }

  return null;
}

function loadEnvScript() {
  const ehModule = getModule();

  if (!ehModule) {
    return;
  }

  const currentProject = Ratchet.observable('project').get();
  const currentBranch = Ratchet.observable('branch').get();

  if (!(currentProject && currentBranch)) {
    return;
  }

  const env = getEnvFromSessionStorage() || getEnvFromBranch(currentBranch);

  const originalExecIndex = Ratchet.AbstractOneTeamPage.prototype.executeIndex;
  const queuedExecIndexCalls = [];

  Ratchet.AbstractOneTeamPage.prototype.executeIndex = function overriddenExecuteIndex(...args) {
    queuedExecIndexCalls.push({
      context: this,
      args
    });
  };

  const onLoad = () => {
    queuedExecIndexCalls.forEach(({
      context,
      args
    }) => {
      originalExecIndex.apply(context, args);
    });

    Ratchet.AbstractOneTeamPage.prototype.executeIndex = originalExecIndex;
  };

  requirejs([getEnvUrl({
    env,
    module: ehModule,
    projectId: currentProject.getId()
  })], onLoad, onLoad);
}

function loadEnv() {
  let currentBranch = Ratchet.observable('branch').get();

  const reload = (newBranch) => {
    const currentBranchId = currentBranch && currentBranch._doc;
    const newBranchId = newBranch && newBranch._doc;
    const loadingTheSameBranch = currentBranchId === newBranchId;

    if (!newBranch || loadingTheSameBranch) {
      return;
    }

    // Super janky, but triggering a refresh to ensure UI is updated with overrides
    // and to clear out any of our modules js from the previous branch
    window.location.reload();
  };

  Ratchet.subscribe('branch', reload);

  loadEnvScript();
}

loadEnv();
